import { Component } from "@angular/core";
import {
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";

/**
 * Generated class for the UsertranferrecordPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-usertranferrecord",
  templateUrl: "usertranferrecord.html",
})
export class UsertranferrecordPage {
  public uid: any;
  public role: any;
  public name: any;
  public credit: any;

  public user: any;
  public loading: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public db: ServiceProvider,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController
  ) {
    this.uid = localStorage.getItem("uidwallet");
    this.role = localStorage.getItem("role");
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem("uidwallet");
        await this.db
          .getServiceJson({ type: "tranferrecordbyid", user_id: this.uid })
          .then(async (data: any) => {
            // console.log(data);
            if (data != null) {
              if (data.status == 200) {
                this.user = await data.result;
                this.loading.dismiss();
              } else {
                this.user = [];
                this.loading.dismiss();
                this.notifications("ไม่มีข้อมูล");
              }
            }
          });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    console.log("ionViewDidLoad UsertranferrecordPage");

    if (this.role == "USER") {
      await this.db
        .getServiceJson({ type: "tranfer", user_id: this.uid })
        .then(async (data: any) => {
          // console.log(data);
          if (data != null) {
            if (data.status == 200) {
              this.name = await data.result.username;
              this.credit = await (data.result.amounts / 100);
            } else {
              this.notifications("ไม่มีข้อมูล");
            }
          }
        });
    }

    await this.db
      .getServiceJson({ type: "tranferrecordbyid", user_id: this.uid })
      .then(async (data: any) => {
        // console.log(data);
        if (data != null) {
          if (data.status == 200) {
            this.user = await data.result;
          } else {
            this.user = [];
            this.notifications("ไม่มีข้อมูล");
          }
        }
      });
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



