import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertController, App, NavController, ToastController } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';
import { HomePage } from '../home/home';


/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  public loginForm: FormGroup;
  public loginError: any;

  constructor(
    public app: App,
    public navCtrl: NavController,
    public db: ServiceProvider,
    public formBuilder: FormBuilder,
    public toastCtrl: ToastController,
    public alertCtrl: AlertController
  ) {
    this.loginForm = formBuilder.group({
      username: ['', Validators.compose([Validators.required])],
      password: ['', Validators.compose([Validators.required])]
    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  async login() {

    localStorage.setItem('uidwallet', null);
    localStorage.setItem('pcheck', null);
    localStorage.setItem('role', null);
    let data = this.loginForm.value;

    if (!data.username) {
      return;
    }

    let credentials = {
      type: "login",
      username: data.username,
      password: data.password
    };

    await this.db.getServiceJson(credentials).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status === 200) {
          if (data.result === 401) {
            this.loginError = "อีเมลหรือชื่อผู้ใช้ไม่ถูกต้อง";
          } else if (data.result === 402) {
            this.loginError = "รหัสผ่านไม่ถูกต้อง";
          } else {
            await localStorage.setItem('uidwallet', await data.result.user_id);
            await localStorage.setItem('pcheck', await data.result.password);
            await localStorage.setItem('role', await data.result.role);
            await this.navCtrl.setRoot(HomePage);
          }
        }
      }
    });

  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: 'bottom'
    });
    await toast.present();
  }

  async reset() {
    localStorage.clear();
    this.app.getRootNavs()[0].setRoot(LoginPage);
  }

}



