import { Component } from "@angular/core";
import {
  AlertController,
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";

/**
 * Generated class for the UserdetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-userdetail",
  templateUrl: "userdetail.html",
})
export class UserdetailPage {
  public uid: any;
  public role: any;
  public name: any;
  public credit: any;

  public banks: any;
  public device: any;
  public loading: any;

  user: any = {
    username: "",
    name: "",
    phone: "",
    nickname: "",
    createdate: "",
    bank_name: "",
    bank_number: "",
  };

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public db: ServiceProvider,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController
  ) {
    this.uid = localStorage.getItem("uidwallet");
    this.role = localStorage.getItem("role");
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        await this.db
          .getServiceJson({ type: "account", user_id: this.uid })
          .then(async (data: any) => {
            // console.log(data.result);
            if (data != null) {
              if (data.status == 200) {
                this.user = await data.result;
                this.loading.dismiss();
              } else {
                this.user = [];
                this.loading.dismiss();
                this.notifications("ไม่มีข้อมูลผู้ใช้งาน");
              }
            }
          });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    console.log("ionViewDidLoad UserdetailPage");

    if (this.role == "USER") {
      await this.db
        .getServiceJson({ type: "tranfer", user_id: this.uid })
        .then(async (data: any) => {
          // console.log(data);
          if (data != null) {
            if (data.status == 200) {
              this.name = await data.result.username;
              this.credit = await (data.result.amounts / 100);
            } else {
              this.notifications("ไม่มีข้อมูล");
            }
          }
        });
    }

    await this.db
      .getServiceJson({ type: "account", user_id: this.uid })
      .then(async (data: any) => {
        // console.log(data.result);
        if (data != null) {
          if (data.status == 200) {
            this.user = await data.result;
          } else {
            this.user = [];
            this.notifications("ไม่มีข้อมูลผู้ใช้งาน");
          }
        }
      });

    await this.db
      .getServiceJson({ type: "allbanks" })
      .then(async (data: any) => {
        // console.log(data.result);
        if (data != null) {
          if (data.status == 200) {
            this.banks = await data.result;
          } else {
            this.banks = [];
            this.notifications("ไม่พบข้อมูล");
          }
        }
      });
  }

  async updateaccount(subtitle: string) {
    const confirm = await this.alertCtrl.create({
      title: '<img src="../../assets/background/logo.png" />',
      subTitle: subtitle,
      message: "คุณต้องการ" + subtitle + "ใช่หรือไม่?",
      cssClass: "alertCustomCssdetail",
      buttons: [
        {
          text: "OK",
          handler: () => {
            let credentials = {
              type: "updateuser",
              username: this.user.username,
              password: this.user.password,
              name: this.user.name,
              phone: this.user.phone,
              nickname: this.user.nickname,
              bank_name: this.user.bank_name,
              bank_number: this.user.bank_number,
              createdate: this.user.createdate,
              user_id: this.uid,
            };

            // console.log(credentials);

            this.db.getServiceJson(credentials).then(async (data: any) => {
              // console.log(data);
              if (data.status == 200) {
                if (data.result) {
                  this.notifications("อัพเดจข้อมูลสำเร็จ");
                } else {
                  this.notifications("อัพเดจข้อมูลไม่สำเร็จ");
                }
              } else {
                this.notifications("ไม่มีข้อมูลเครื่อง");
              }
            });
          },
        },
        {
          text: "CANCEL",
          handler: async () => {},
        },
      ],
    });
    await confirm.present();
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



