import { Component } from '@angular/core';
import { LoadingController, NavController, NavParams, ToastController } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';

/**
 * Generated class for the ManagementPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */


@Component({
  selector: 'page-tranferrecorddetail',
  templateUrl: 'tranferrecorddetail.html',
})
export class TranferrecorddetailPage {

  public uid: any;
  public user: any;
  public loading: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public db: ServiceProvider,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController) {

    this.uid = localStorage.getItem('uidwallet');

  }


  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem('uidwallet');
        await this.db.getServiceJson({ type: "tranferrecordbyid", user_id: this.navParams.get('uid') }).then(async (data: any) => {
          // console.log(data);
          if (data != null) {
            if (data.status == 200) {
              this.user = data.result;
              this.loading.dismiss();
            } else {
              this.user = [];
              this.loading.dismiss();
              this.notifications('ไม่มีข้อมูล');
            }
          }
        });
      });
      refresher.complete();
    }, 1000);
  }

  async ionViewDidLoad() {
    console.log('ionViewDidLoad TranferrecorddetailPage');
    await this.db.getServiceJson({ type: "tranferrecordbyid", user_id: this.navParams.get('uid') }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.user = await data.result;
        } else {
          this.user = [];
          this.notifications('ไม่มีข้อมูล');
        }
      }
    });
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: 'bottom'
    });
    await toast.present();
  }

}



