import { Component } from '@angular/core';
import { AlertController, App, NavController, ToastController } from 'ionic-angular';
import { AdduserPage } from '../adduser/adduser';
import { ManagementPage } from '../management/management';
import { TranferPage } from '../tranfer/tranfer';
import { TranferrecordPage } from '../tranferrecord/tranferrecord';
import { AccountPage } from '../account/account';
import { UserdetailPage } from '../userdetail/userdetail';
import { UserdevicedetailPage } from '../userdevicedetail/userdevicedetail';

import { UsertranferrecordPage } from '../usertranferrecord/usertranferrecord';
import { ReportPage } from '../report/report';
import { UserreportPage } from '../userreport/userreport';
import { LoginPage } from '../login/login';
import { ServiceProvider } from '../../providers/service/service';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  public uid: any;
  public role: any;
  public name: any;
  public credit: any;

  constructor(public app: App, public navCtrl: NavController, public alertCtrl: AlertController, public db: ServiceProvider, public toastCtrl: ToastController) {
    this.uid = localStorage.getItem('uidwallet');
    this.role = localStorage.getItem('role');
  }


  async ionViewDidLoad() {
    console.log('ionViewDidLoad TranferrecorddetailPage');
    if (this.role == "USER") {
      await this.db.getServiceJson({ type: "tranfer", user_id: this.uid }).then(async (data: any) => {
        // console.log(data);
        if (data != null) {
          if (data.status == 200) {
            this.name = await data.result.username;
            this.credit = await (data.result.amounts / 100);
          } else {
            this.notifications('ไม่มีข้อมูล');
          }
        }
      });
    }
  }

  async menu(selecter) {
    if (this.role == "ADMIN") {
      switch (selecter) {
        case 1:
          await this.navCtrl.push(AdduserPage);
          break;
        case 2:
          await this.navCtrl.push(ManagementPage);
          break;
        case 3:
          await this.navCtrl.push(TranferPage);
          break;
        case 4:
          await this.navCtrl.push(TranferrecordPage);
          break;
        case 5:
          await this.navCtrl.push(ReportPage);
          break;
        case 6:
          await this.navCtrl.push(AccountPage);
          break;
        case 7:
          await this.logout();
          break;
      }
    } else if (this.role == "USER") {
      switch (selecter) {
        case 1:
          await this.navCtrl.push(UserdetailPage);
          break;
        case 2:
          await this.navCtrl.push(UserdevicedetailPage);
          break;
        case 3:
          await this.navCtrl.push(UsertranferrecordPage);
          break;
        case 4:
          await this.navCtrl.push(UserreportPage);
          break;
        case 5:
          await this.navCtrl.push(AccountPage);
          break;
        case 6:
          await this.logout();
          break;
      }
    }
  }


  async logout() {
    const confirm = await this.alertCtrl.create({
      title: '<img src="../../assets/background/logo.png" />',
      subTitle: 'ออกจากระบบ',
      message: 'ต้องการออกจากระบบใช่หรือไม่?',
      cssClass: 'alertCustomCssdetail',
      buttons: [
        {
          text: 'ตกลง ออกจากระบบ',
          handler: () => {
            localStorage.clear();
            this.app.getRootNavs()[0].setRoot(LoginPage);
          }
        },
        {
          text: 'ยกเลิก',
          handler: async () => {

          }
        }
      ]
    });
    await confirm.present();
  }


  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: 'bottom'
    });
    await toast.present();
  }
}



