import { Component } from "@angular/core";
import {
  AlertController,
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";
import { AdddevicePage } from "../adddevice/adddevice";

/**
 * Generated class for the ManagementdetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-managementdetail",
  templateUrl: "managementdetail.html",
})
export class ManagementdetailPage {
  public uid: any;
  public banks: any;
  public device: any;
  public loading: any;

  user: any = {
    username: "",
    name: "",
    phone: "",
    nickname: "",
    createdate: "",
    bank_name: "",
    bank_number: "",
  };

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public db: ServiceProvider,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController
  ) {
    this.uid = localStorage.getItem("uidwallet");

    this.db
      .getServiceJson({ type: "account", user_id: this.navParams.get("uid") })
      .then(async (data: any) => {
        // console.log(data.result);
        if (data != null) {
          if (data.status == 200) {
            this.user = await data.result;
          } else {
            this.user = [];
            this.notifications("ไม่มีข้อมูลผู้ใช้งาน");
          }
        }
      });

    this.db
      .getServiceJson({ type: "device", user_id: this.navParams.get("uid") })
      .then(async (data: any) => {
        // console.log(data);
        if (data != null) {
          if (data.status == 200) {
            this.device = await data.result;
          } else {
            this.device = [];
            this.notifications("ไม่มีข้อมูลเครื่อง");
          }
        }
      });
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        await this.db
          .getServiceJson({
            type: "account",
            user_id: this.navParams.get("uid"),
          })
          .then(async (data: any) => {
            // console.log(data.result);
            if (data != null) {
              if (data.status == 200) {
                this.user = await data.result;
              } else {
                this.user = [];
                this.notifications("ไม่มีข้อมูลผู้ใช้งาน");
              }
            }
          });

        await this.db
          .getServiceJson({
            type: "device",
            user_id: this.navParams.get("uid"),
          })
          .then(async (data: any) => {
            // console.log(data);
            if (data != null) {
              if (data.status == 200) {
                this.device = await data.result;
                this.loading.dismiss();
              } else {
                this.device = [];
                this.loading.dismiss();
                this.notifications("ไม่มีข้อมูลเครื่อง");
              }
            }
          });
      });

      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    console.log("ionViewDidLoad ManagementdetailPage");
    await this.db
      .getServiceJson({ type: "allbanks" })
      .then(async (data: any) => {
        // console.log(data.result);
        if (data != null) {
          if (data.status == 200) {
            this.banks = await data.result;
          } else {
            this.banks = [];
            this.notifications("ไม่พบข้อมูล");
          }
        }
      });
  }

  async updateaccount(subtitle: string) {
    const confirm = await this.alertCtrl.create({
      title: '<img src="../../assets/background/logo.png" />',
      subTitle: subtitle,
      message: "ต้องการอัพเดทข้อมูลใช่หรือไม่?",
      cssClass: "alertCustomCssdetail",
      buttons: [
        {
          text: "OK",
          handler: () => {
            let credentials = {
              type: "updateuser",
              username: this.user.username,
              password: this.user.password,
              name: this.user.name,
              phone: this.user.phone,
              nickname: this.user.nickname,
              bank_name: this.user.bank_name,
              bank_number: this.user.bank_number,
              createdate: this.user.createdate,
              user_id: this.navParams.get("uid"),
            };

            // console.log(credentials);

            this.db.getServiceJson(credentials).then(async (data: any) => {
              // console.log(data);
              if (data.status == 200) {
                if (data.result) {
                  this.notifications("อัพเดทข้อมูลสำเร็จ");
                } else {
                  this.notifications("อัพเดทข้อมูลไม่สำเร็จ");
                }
              } else {
                this.notifications("ไม่มีข้อมูลเครื่อง");
              }
            });
          },
        },
        {
          text: "CANCEL",
          handler: async () => {},
        },
      ],
    });
    await confirm.present();
  }

  async adddevice(subtitle: string) {
    const confirm = await this.alertCtrl.create({
      title: '<img src="../../assets/background/logo.png" />',
      subTitle: subtitle,
      message: "คุณต้องการ" + subtitle + "อุปกรณ์ใช่หรือไม่?",
      cssClass: "alertCustomCssdetail",
      buttons: [
        {
          text: "OK",
          handler: () => {
            this.navCtrl.push(AdddevicePage, {
              user_id: this.navParams.get("uid"),
            });
          },
        },
        {
          text: "CANCEL",
          handler: async () => {},
        },
      ],
    });
    await confirm.present();
  }

  async removedevice(subtitle: string, data) {
    const confirm = await this.alertCtrl.create({
      title: '<img src="../../assets/background/logo.png" />',
      subTitle: subtitle,
      message: "คุณต้องการ" + subtitle + "อุปกรณ์ใช่หรือไม่?",
      cssClass: "alertCustomCssdetail",
      inputs: [
        {
          name: "password",
          placeholder: "กรอกรหัสผ่าน",
          type: "text",
        },
      ],
      buttons: [
        {
          text: "OK",
          handler: (inputs) => {
            // console.log(localStorage.getItem('pcheck'));

            if (localStorage.getItem("pcheck") != inputs.password) {
              this.notifications(
                "รหัสผ่านไม่ถูกต้อง รหัส:" + localStorage.getItem("pcheck")
              );
            } else {
              let credentials = {
                type: "removedevice",
                device_id: data.device_id,
              };

              // console.log(credentials);
              this.db.getServiceJson(credentials).then((data: any) => {
                // console.log(data);
                if (data != null) {
                  if (data.status == 200) {
                    if (data.result) {
                      this.notifications("ลบข้อมูลสำเร็จ");
                      // localStorage.setItem('pcheck', 'null');
                    } else {
                      this.notifications("ลบข้อมูลไม่สำเร็จ");
                    }
                  } else {
                    this.notifications("ไม่มีข้อมูลเครื่อง");
                  }
                }
              });
            }
          },
        },
        {
          text: "CANCEL",
          handler: async () => {},
        },
      ],
    });
    await confirm.present();
  }

  async removeuser(subtitle: string, user_id) {
    const confirm = await this.alertCtrl.create({
      title: '<img src="../../assets/background/logo.png" />',
      subTitle: subtitle,
      message: "คุณต้องการ" + subtitle + "ใช่หรือไม่?",
      cssClass: "alertCustomCssdetail",
      inputs: [
        {
          name: "password",
          placeholder: "กรอกรหัสผ่าน",
          type: "text",
        },
      ],
      buttons: [
        {
          text: "OK",
          handler: (inputs) => {
            // console.log(localStorage.getItem('pcheck'));

            if (localStorage.getItem("pcheck") != inputs.password) {
              this.notifications(
                "รหัสผ่านไม่ถูกต้อง รหัส:" + localStorage.getItem("pcheck")
              );
            } else {
              let credentials = {
                type: "removeuser",
                user_id: user_id,
              };

              // console.log(credentials);

              this.db.getServiceJson(credentials).then((data: any) => {
                // console.log(data);
                if (data != null) {
                  if (data.status == 200) {
                    if (data.result) {
                      this.notifications("ลบข้อมูลสำเร็จ");
                      // localStorage.setItem('pcheck', 'null');
                      this.navCtrl.pop();
                    } else {
                      this.notifications("ลบข้อมูลไม่สำเร็จ");
                    }
                  } else {
                    this.notifications("ไม่มีข้อมูลเครื่อง");
                  }
                }
              });
            }
          },
        },
        {
          text: "CANCEL",
          handler: async () => {},
        },
      ],
    });
    await confirm.present();
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



