import { Component } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import {
  AlertController,
  LoadingController,
  NavController,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";
import * as moment from "moment";
import { format } from "date-fns";

/**
 * Generated class for the UserreportPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-userreport",
  templateUrl: "userreport.html",
})
export class UserreportPage {
  public momentjs: any = moment;
  public date: any;
  public time: any;
  public form: any;
  public to: any;

  public formdisplay: any;
  public todisplay: any;

  public display: any;
  public device: any;
  public totalts: any;
  public totalic: any;

  public uid: any;
  public user_id: any;
  public loading: any;
  public user: any;
  public role: any;
  public name: any;
  public credit: any;
  public adduserForm: FormGroup;
  public adduserError: string;

  public datecheck: boolean = false;

  constructor(
    public navCtrl: NavController,
    public db: ServiceProvider,
    public formBuilder: FormBuilder,
    public toastCtrl: ToastController,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController
  ) {
    this.adduserForm = formBuilder.group({
      // form: ["2023-05-21", Validators.compose([Validators.required])],
      form: [
        this.momentjs().format("YYYY-MM-DD"),
        Validators.compose([Validators.required]),
      ],
      to: [
        this.momentjs().format("YYYY-MM-DD"),
        Validators.compose([Validators.required]),
      ],
    });

    this.uid = localStorage.getItem("uidwallet");
    this.role = localStorage.getItem("role");
    this.date = this.momentjs().format("DD/MM/YYYY");
    this.time = this.momentjs().format("HH:mm:ss");
    this.device = [];
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem("uidwallet");
        this.role = localStorage.getItem("role");
        this.date = this.momentjs().format("DD/MM/YYYY");
        this.time = this.momentjs().format("HH:mm:ss");
        this.device = [];

        this.loading.dismiss();
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    console.log("ionViewDidLoad UserreportPage");

    if (this.role == "USER") {
      await this.db
        .getServiceJson({ type: "tranfer", user_id: this.uid })
        .then(async (data: any) => {
          // console.log(data);
          if (data != null) {
            if (data.status == 200) {
              this.name = await data.result.username;
              this.credit = await (data.result.amounts / 100);
            } else {
              this.notifications("ไม่มีข้อมูล");
            }
          }
        });
    }
  }

  async search() {
    let data = this.adduserForm.value;

    this.form = format(new Date(data.form), "yyyy-MM-dd");
    this.to = format(new Date(data.to), "yyyy-MM-dd");

    // change date format for display
    this.formdisplay = format(new Date(data.form), "dd/MM/yyyy");
    this.todisplay = format(new Date(data.to), "dd/MM/yyyy");

    this.db
      .getServiceJson({
        type: "perweekreport",
        user_id: this.uid,
        form: this.form,
        to: this.to,
      })
      .then(async (data: any) => {
        // console.log(data);
        if (data != null) {
          if (data.status == 200) {
            this.device = data.result;
            // console.log(this.device);
            this.TotalAll(this.uid, this.form, this.to).then((res: any) => {
              // console.log(res);
              if (isNaN(res.totalincome) && isNaN(res.totaltransactions)) {
                this.notifications("ไม่พบข้อมูล");
              } else {
                // console.log(res);
                this.totalic = res.totalincome;
                this.totalts = res.totaltransactions;
              }
            });
          } else {
            this.device = [];
            this.notifications("ไม่พบข้อมูล");
          }
        } else {
          this.device = [];
          this.notifications("ไม่พบข้อมูล");
        }
      });
  }

  async TotalAll(uid, form, to) {
    return new Promise((resolve, reject) => {
      this.db
        .getServiceJson({
          type: "perweekreport",
          user_id: uid,
          form: form,
          to: to,
        })
        .then(
          async (data: any) => {
            if (data != null) {
              if (data.status == 200) {
                let income = [];
                let transactions = [];
                let totalincome = 0.0;
                let totaltransactions = 0.0;

                await data.result.forEach(async (element) => {
                  income.push(await element["income_money"]);
                  transactions.push(await element["transaction"]);
                });

                await income.forEach(async (element) => {
                  totalincome += parseInt(element);
                });

                await transactions.forEach(async (element) => {
                  totaltransactions += parseInt(element);
                });

                resolve({
                  totalincome: totalincome,
                  totaltransactions: totaltransactions,
                });

                income = [];
                transactions = [];
                totalincome = 0.0;
                totaltransactions = 0.0;
              } else {
                resolve({
                  totalincome: 0,
                  totaltransactions: 0,
                });
              }
            } else {
              resolve({
                totalincome: 0,
                totaltransactions: 0,
              });
            }
          },
          (error) => {
            reject(false);
          }
        );
    });
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



