import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertController, NavController, ToastController } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';
import * as moment from 'moment';
import { format } from 'date-fns';

/**
 * Generated class for the AdduserPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-adduser',
  templateUrl: 'adduser.html',
})
export class AdduserPage {

  public momentjs: any = moment;
  public date: any;
  public time: any;

  public uid: any;
  public banks: any;
  public role: any;

  public adduserForm: FormGroup;
  public adduserError: string;

  constructor(
    public navCtrl: NavController,
    public db: ServiceProvider,
    public formBuilder: FormBuilder,
    public toastCtrl: ToastController,
    public alertCtrl: AlertController
  ) {
    this.adduserForm = formBuilder.group({
      username: ['', Validators.compose([Validators.required])],
      password: ['', Validators.compose([Validators.required])],
      name: ['', Validators.compose([Validators.required])],
      phone: ['', Validators.compose([Validators.required])],
      nickname: ['', Validators.compose([Validators.required])],
      bank_name: ['', Validators.compose([Validators.required])],
      bank_number: ['', Validators.compose([Validators.required])],
      createdate: [this.momentjs().format('YYYY-MM-DD'), Validators.compose([Validators.required])]
    });

    this.role = 'USER';
    this.uid = localStorage.getItem('uidwallet');
    this.db.getServiceJson({ type: "allbanks" }).then(async (data: any) => {
      // console.log(data.result);
      if (data != null) {
        if (data.status == 200) {
          this.banks = await data.result;
        } else {
          this.banks = [];
          this.notifications("ไม่พบข้อมูล");
        }
      }
    });
  }

  async ionViewDidLoad() {
    console.log('ionViewDidLoad AdduserPage');
    await this.db.getServiceJson({ type: "allbanks" }).then(async (data: any) => {
      // console.log(data.result);
      if (data != null) {
        if (data.status == 200) {
          this.banks = await data.result;
        } else {
          this.banks = [];
          this.notifications("ไม่พบข้อมูล");
        }
      }
    });
  }


  async adduser() {
    const confirm = await this.alertCtrl.create({
      title: '<img src="../../assets/background/logo.png" />',
      message: 'คุณต้องการเพิ่มผู้ใช้งานใช่หรือไม่?',
      cssClass: 'alertCustomCssdetail',
      buttons: [
        {
          text: 'OK',
          handler: () => {

            let data = this.adduserForm.value;
            let credentials = {
              type: "adduser",
              username: data.username,
              password: data.password,
              name: data.name,
              phone: data.phone,
              nickname: data.nickname,
              bank_name: data.bank_name,
              bank_number: data.bank_number,
              role: this.role,
              createdate: format(new Date(data.createdate), 'dd/MM/yyyy')
            };

            // console.log(credentials);
            this.db.getServiceJson(credentials).then(async (data: any) => {
              if (data.status === 200) {
                if (data.result) {
                  this.notifications('สมัครสมาชิกสำเร็จ')
                  this.navCtrl.pop();
                }
              } else {
                this.adduserError = "อีเมลถูกใช้ไปแล้ว";
              }
            });
          }
        },
        {
          text: 'CANCEL',
          handler: async () => {

          }
        }
      ]
    });
    await confirm.present();
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: 'bottom'
    });
    await toast.present();
  }

}



