import { Component } from '@angular/core';
import { AlertController, LoadingController, NavController, NavParams, ToastController } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';


/**
 * Generated class for the UserdevicedetailPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-userdevicedetail',
  templateUrl: 'userdevicedetail.html',
})
export class UserdevicedetailPage {

  public uid: any;
  public role: any;
  public name: any;
  public credit: any;

  public banks: any;
  public device: any;
  public loading: any;

  user: any = {
    username: '',
    name: '',
    phone: '',
    nickname: '',
    createdate: '',
    bank_name: '',
    bank_number: ''
  }

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public db: ServiceProvider,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController
  ) {

    this.uid = localStorage.getItem('uidwallet');
    this.role = localStorage.getItem('role');

  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        await this.db.getServiceJson({ type: "device", user_id: this.uid }).then(async (data: any) => {
          // console.log(data);
          if (data != null) {
            if (data.status == 200) {
              this.device = await data.result;
              this.loading.dismiss();
            } else {
              this.device = [];
              this.loading.dismiss();
              this.notifications('ไม่มีข้อมูลเครื่อง');
            }
          }
        });

      });

      refresher.complete();
    }, 1000);
  }


  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  
  async ionViewDidLoad() {
    console.log('ionViewDidLoad UserdevicedetailPage');

    if (this.role == "USER") {
      await this.db.getServiceJson({ type: "tranfer", user_id: this.uid }).then(async (data: any) => {
        // console.log(data);
        if (data != null) {
          if (data.status == 200) {
            this.name = await data.result.username;
            this.credit = await (data.result.amounts / 100);
          } else {
            this.notifications('ไม่มีข้อมูล');
          }
        }
      });
    }

    await this.db.getServiceJson({ type: "device", user_id: this.uid }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.device = await data.result;
        } else {
          this.device = [];
          this.notifications('ไม่มีข้อมูลเครื่อง');
        }
      }
    });
  }


  async editnamedevice(subtitle: string, data) {
    const confirm = await this.alertCtrl.create({
      title: '<img src="../../assets/background/logo.png" />',
      subTitle: subtitle,
      message: 'คุณต้องการ' + subtitle + 'ใช่หรือไม่?',
      cssClass: 'alertCustomCssdetail',
      buttons: [
        {
          text: 'OK',
          handler: () => {
            // console.log(data.device_id);

            let credentials = {
              type: "updatenamedevice",
              boardname: data.boardname,
              device_id: data.device_id
            };

            // console.log(credentials);

            this.db.getServiceJson(credentials).then(async (data: any) => {
              // console.log(data);
              if (data.status == 200) {
                if (data.result) {
                  this.notifications('อัพเดจข้อมูลสำเร็จ');
                } else {
                  this.notifications('อัพเดจข้อมูลไม่สำเร็จ');
                }
              } else {
                this.notifications('ไม่มีข้อมูลเครื่อง');
              }
            });

          }
        },
        {
          text: 'CANCEL',
          handler: async () => {
          }
        }
      ]
    });
    await confirm.present();
  }


  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: 'bottom'
    });
    await toast.present();
  }

}



