import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { AccountPage } from '../pages/account/account';
import { ServiceProvider } from '../providers/service/service';
import { HttpClientModule } from '@angular/common/http';
import { ManagementPage } from '../pages/management/management';
import { ManagementdetailPage } from '../pages/managementdetail/managementdetail';
import { TranferPage } from '../pages/tranfer/tranfer';
import { TranferrecordPage } from '../pages/tranferrecord/tranferrecord';
import { TranferrecorddetailPage } from '../pages/tranferrecorddetail/tranferrecorddetail';
import { AdduserPage } from '../pages/adduser/adduser';
import { AdddevicePage } from '../pages/adddevice/adddevice';
import { UserdetailPage } from '../pages/userdetail/userdetail';
import { UserdevicedetailPage } from '../pages/userdevicedetail/userdevicedetail';
import { UsertranferrecordPage } from '../pages/usertranferrecord/usertranferrecord';
import { UserreportPage } from '../pages/userreport/userreport';
import { ReportPage } from '../pages/report/report';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    AdduserPage,
    AccountPage,
    ManagementPage,
    ManagementdetailPage,
    TranferPage,
    TranferrecordPage,
    TranferrecorddetailPage,
    ReportPage,
    AdddevicePage,
    UserdetailPage,
    UserdevicedetailPage,
    UsertranferrecordPage,
    UserreportPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    IonicModule.forRoot(MyApp, { mode: 'ios' })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    AdduserPage,
    AccountPage,
    ManagementPage,
    ManagementdetailPage,
    TranferPage,
    TranferrecordPage,
    TranferrecorddetailPage,
    ReportPage,
    AdddevicePage,
    UserdetailPage,
    UserdevicedetailPage,
    UsertranferrecordPage,
    UserreportPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    ServiceProvider
  ]
})
export class AppModule { }



