import { Component } from "@angular/core";
import {
  AlertController,
  LoadingController,
  NavController,
  NavParams,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";
import * as moment from "moment";

/**
 * Generated class for the TranferPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-tranfer",
  templateUrl: "tranfer.html",
})
export class TranferPage {
  public uid: any;

  public momentjs: any = moment;
  public date: any;
  public time: any;

  public device: any;
  public loading: any;
  public fullname: any;
  public totalsum: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public db: ServiceProvider,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController
  ) {
    this.uid = localStorage.getItem("uidwallet");

    this.date = this.momentjs().format("YYYY-MM-DD");
    this.time = this.momentjs().format("HH:mm:ss");

    this.db.getServiceJson({ type: "alltranfer" }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.device = await data.result;
        } else {
          this.device = [];
          this.notifications("ไม่มีข้อมูล");
        }
      }
    });

    this.totals().then(async (res: any) => {
      // console.log(res);
      if (isNaN(res)) {
        this.notifications("ไม่มีข้อมูล");
      } else {
        this.totalsum = await res;
      }
    });
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem("uidwallet");

        this.date = this.momentjs().format("DD/MM/YYYY");
        this.time = this.momentjs().format("HH:mm:ss");

        await this.db
          .getServiceJson({ type: "alltranfer" })
          .then(async (data: any) => {
            // console.log(data);
            if (data != null) {
              if (data.status == 200) {
                this.device = await data.result;
                this.loading.dismiss();
              } else {
                this.device = [];
                this.loading.dismiss();
                this.notifications("ไม่มีข้อมูล");
              }
            }
          });

        this.totals().then(async (res: any) => {
          // console.log(res);
          if (isNaN(res)) {
            this.notifications("ไม่มีข้อมูล");
          } else {
            this.totalsum = await res;
          }
        });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async totals() {
    return new Promise((resolve, reject) => {
      this.db.getServiceJson({ type: "alltranfer" }).then(
        async (data: any) => {
          if (data != null) {
            if (data.status == 200) {
              let balance = [];
              let totalmoney = 0.0;
              await data.result.forEach(async (element) => {
                balance.push((await element["amounts"]) / 100);
              });
              await balance.forEach(async (element) => {
                totalmoney += parseInt(element);
              });

              resolve(totalmoney);
              balance = [];
              totalmoney = 0.0;
            } else {
              let totalmoney = 0.0;
              resolve(totalmoney);
            }
          } else {
            let totalmoney = 0.0;
            resolve(totalmoney);
          }
        },
        (error) => {
          reject(false);
        }
      );
    });
  }

  async ionViewDidLoad() {
    console.log("ionViewDidLoad TranferPage");
  }

  async deposit(subtitle: string, data) {
    const confirm = await this.alertCtrl.create({
      title: '<img src="../../assets/background/logo.png" />',
      subTitle: subtitle,
      message: "ระบุจำนวน Credit ที่ต้องการฝาก",
      cssClass: "alertCustomCssdetail",
      inputs: [
        {
          name: "amounts",
          placeholder: "โปรดกรอกจำนวนเงิน",
          type: "number",
        },
      ],
      buttons: [
        {
          text: "OK",
          handler: (inputs) => {
            if (inputs.amounts == "") {
              this.notifications("โปรดกรอกจำนวนเงิน");
            } else {
              // console.log(inputs.amounts);

              this.db
                .getServiceJson({
                  type: "deposit",
                  user_id: data.user_id,
                  operation_id: "ADMIN",
                  macaddress: "ADMIN",
                  amounts: inputs.amounts * 100,
                  ws_id: data.ws_id,
                  date: this.momentjs().format("YYYY-DD-MM"),
                  time: this.momentjs().format("HH:mm:ss"),
                })
                .then(async (data: any) => {
                  console.log(data);
                  if (data != null) {
                    if (data.status == 200) {
                      this.notifications("ฝากเงินสำเร็จ");
                      // this.navCtrl.pop();
                      this.autoRefresh();
                    }
                  }
                });
            }
          },
        },
        {
          text: "CANCEL",
          handler: async () => {},
        },
      ],
    });
    await confirm.present();
  }

  async withdraw(subtitle: string, data) {
    if (data.amounts / 100 == 0) {
      this.notifications("ยอดเงินไม่เพียงพอตต่อการถอน");
    } else {
      const confirm = await this.alertCtrl.create({
        title: '<img src="../../assets/background/logo.png" />',
        subTitle: subtitle,
        message: "ระบุจำนวน Credit ที่ต้องการถอน",
        cssClass: "alertCustomCssdetail",
        inputs: [
          {
            name: "amounts",
            placeholder: "โปรดกรอกจำนวนเงิน",
            type: "number",
          },
        ],
        buttons: [
          {
            text: "OK",
            handler: (inputs) => {
              if (inputs.amounts == "") {
                this.notifications("โปรดกรอกจำนวนเงิน");
              } else if (inputs.amounts == 0) {
                this.notifications("โปรดกรอกจำนวนเงินให้มากกว่า 0");
              } else if (inputs.amounts > data.balance / 100) {
                this.notifications("ยอดเงินไม่เพียงพอตต่อการถอน");
              } else {
                // console.log(inputs.amounts);
                this.db
                  .getServiceJson({
                    type: "withdraw",
                    user_id: data.user_id,
                    operation_id: "ADMIN",
                    macaddress: "ADMIN",
                    amounts: inputs.amounts * 100,
                    ws_id: data.ws_id,
                    date: this.momentjs().format("YYYY-DD-MM"),
                    time: this.momentjs().format("HH:mm:ss"),
                  })
                  .then(async (data: any) => {
                    // console.log(data);
                    if (data != null) {
                      if (data.status == 200) {
                        this.notifications("ถอนเงินสำเร็จ");
                        // this.navCtrl.pop();
                        this.autoRefresh();
                      }
                    }
                  });
              }
            },
          },
          {
            text: "CANCEL",
            handler: async () => {},
          },
        ],
      });
      await confirm.present();
    }
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



