import { Component } from '@angular/core';
import { LoadingController, NavController, NavParams, ToastController } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';
import { ManagementdetailPage } from '../managementdetail/managementdetail';

/**
 * Generated class for the ManagementPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-management',
  templateUrl: 'management.html',
})
export class ManagementPage {

  public uid: any;
  public user: any;
  public loading: any;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public db: ServiceProvider,
    public toastCtrl: ToastController,
    public loadingCtrl: LoadingController
  ) {

    this.uid = localStorage.getItem('uidwallet');
    
    this.db.getServiceJson({ type: "allaccount" }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.user = await data.result;
        } else {
          this.notifications('ไม่มีข้อมูลผู้ใช้งาน');
        }
      }
    });
  }

  
  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        this.uid = localStorage.getItem('uidwallet');
        await this.db.getServiceJson({ type: "allaccount" }).then(async (data: any) => {
          if (data != null) {
            if (data.status == 200) {
              this.user = await data.result;
              this.loading.dismiss();
            } else {
              this.user = [];
              this.notifications('ไม่มีข้อมูลผู้ใช้งาน');
              this.loading.dismiss();
            }
          }
        });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  async ionViewDidLoad() {
    console.log('ionViewDidLoad ManagementPage');
    await this.db.getServiceJson({ type: "allaccount" }).then(async (data: any) => {
      // console.log(data);
      if (data != null) {
        if (data.status == 200) {
          this.user = await data.result;
        } else {
          this.user = [];
          this.notifications('ไม่มีข้อมูลผู้ใช้งาน');
        }
      }
    });
  }

  async Account(uid) {
    await this.navCtrl.push(ManagementdetailPage, { uid: uid });
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: 'bottom'
    });
    await toast.present();
  }

}



