import { Component } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import {
  AlertController,
  LoadingController,
  NavController,
  ToastController,
} from "ionic-angular";
import { ServiceProvider } from "../../providers/service/service";
import * as moment from "moment";
import { format } from "date-fns";

/**
 * Generated class for the ReportPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: "page-report",
  templateUrl: "report.html",
})
export class ReportPage {
  public momentjs: any = moment;
  public date: any;
  public time: any;
  public form: any;
  public to: any;

  public formdisplay: any;
  public todisplay: any;

  public display: any;
  public device: any;
  public totalt_tran: any;
  public totalt_amout: any;

  public uid: any;
  public user_id: any;
  public loading: any;
  public user: any;
  public role: any;
  public adduserForm: FormGroup;
  public adduserError: string;

  public datecheck: boolean = false;
  public checkrefresh: any = "all";

  constructor(
    public navCtrl: NavController,
    public db: ServiceProvider,
    public formBuilder: FormBuilder,
    public toastCtrl: ToastController,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController
  ) {
    this.adduserForm = formBuilder.group({
      // form: ["2023-05-21", Validators.compose([Validators.required])],
      form: [
        this.momentjs().format("YYYY-MM-DD"),
        Validators.compose([Validators.required]),
      ],
      to: [
        this.momentjs().format("YYYY-MM-DD"),
        Validators.compose([Validators.required]),
      ],
      uid: ["all", Validators.compose([Validators.required])],
    });

    this.uid = localStorage.getItem("uidwallet");
    this.date = this.momentjs().format("DD/MM/YYYY");
    this.time = this.momentjs().format("HH:mm:ss");
  }

  async doRefresh(refresher) {
    setTimeout(() => {
      this.loading = this.loadingCtrl.create({});
      this.loading.present().then(async () => {
        // console.log(this.checkrefresh);
        if (this.checkrefresh == "all") {
          this.datecheck = true;
        } else {
          this.datecheck = false;
        }

        await this.db
          .getServiceJson({ type: "allaccount" })
          .then(async (data: any) => {
            if (data != null) {
              if (data.status == 200) {
                this.user = await data.result;
                this.device = [];
                this.loading.dismiss();
              } else {
                this.user = [];
                this.notifications("ไม่พบข้อมูล");
              }
            }
          });
      });
      refresher.complete();
    }, 1000);
  }

  async autoRefresh() {
    const refresher = {
      complete: () => {
        console.log("Refresher complete");
      },
    };
    await this.doRefresh(refresher);
  }

  async ionViewWillEnter() {
    console.log("ionViewWillEnter");
    await this.autoRefresh();
  }

  
  async ionViewDidLoad() {
    console.log("ionViewDidLoad ReportPage");
    this.datecheck = true;
    await this.db
      .getServiceJson({ type: "allaccount" })
      .then(async (data: any) => {
        if (data != null) {
          if (data.status == 200) {
            this.user = await data.result;
            this.device = [];
          } else {
            this.user = [];
            this.notifications("ไม่พบข้อมูล");
          }
        }
      });
  }

  async UseronChange(e) {
    // console.log(e);
    this.checkrefresh = e;
    if (e == "all") {
      this.datecheck = true;
      this.device = []; // clear list
      this.totalt_amout = 0; // reset total
      this.totalt_tran = 0; // reset total
    } else {
      this.datecheck = false;
      this.device = []; // clear list
      this.totalt_amout = 0; // reset total
      this.totalt_tran = 0; // reset total
    }
  }

  async search() {
    let data = this.adduserForm.value;
    this.user_id = data.uid;

    if (data.uid == "all") {
      // date format for sql
      this.form = format(new Date(data.form), "yyyy-MM-dd");
      this.to = format(new Date(data.to), "yyyy-MM-dd");

      // change date format for display
      this.formdisplay = format(new Date(data.form), "dd/MM/yyyy");
      this.todisplay = format(new Date(data.to), "dd/MM/yyyy");

      // display data
      this.display = true;

      this.db.getServiceJson({
          type: "allreport",
          form: this.form,
          to: this.to,
        })
        .then(async (data: any) => {
          // console.log(data); //dbug
          if (data != null) {
            if (data.status == 200) {
              this.device = data.result;
              // call cal
              this.TotalAll(this.form, this.to).then((res: any) => {
                if (
                  isNaN(res.totalincome_all) &&
                  isNaN(res.totaltransaction_all)
                ) {
                  this.notifications("ไม่พบข้อมูล");
                } else {
                  // console.log(res);
                  this.totalt_amout = res.totalincome_all;
                  this.totalt_tran = res.totaltransaction_all;
                }
              });
            } else {
              this.device = [];
              this.notifications("ไม่พบข้อมูล");
            }
          } else {
            this.device = [];
            this.notifications("ไม่พบข้อมูล");
          }
        });
    } else {
      // date format for sql
      this.form = format(new Date(data.form), "yyyy-MM-dd");
      this.to = format(new Date(data.to), "yyyy-MM-dd");

      // change date format for display
      this.formdisplay = format(new Date(data.form), "dd/MM/yyyy");
      this.todisplay = format(new Date(data.to), "dd/MM/yyyy");

      // display data
      this.display = false;

      this.db
        .getServiceJson({
          type: "perdayreport",
          user_id: this.user_id,
          form: this.form,
          to: this.to,
        })
        .then(async (data: any) => {
          // console.log(data); //dbug
          if (data != null) {
            if (data.status == 200) {
              // let in html
              this.device = data.result;
              // call cal
              this.TotalPerDay(this.user_id, this.form, this.to).then(
                (res: any) => {
                  // console.log(res);
                  if (isNaN(res.totalincome) && isNaN(res.totaltransaction)) {
                    this.notifications("ไม่พบข้อมูล");
                  } else {
                    // console.log(res);
                    this.totalt_amout = res.totalincome;
                    this.totalt_tran = res.totaltransaction;
                  }
                }
              );
            } else {
              this.device = [];
              this.notifications("ไม่พบข้อมูล");
            }
          }else {
            this.device = [];
            this.notifications("ไม่พบข้อมูล");
          }
        });
    }
  }

  async TotalAll(form, to) {
    return new Promise((resolve, reject) => {
      this.db.getServiceJson({ type: "allreport", form: form, to: to }).then(
        async (data: any) => {
          if (data != null) {
            if (data.status == 200) {
              let income_all = [];
              let transaction_all = [];
              let totalincome_all = 0.0;
              let totaltransaction_all = 0.0;

              await data.result.forEach(async (element) => {
                income_all.push(await element["income_money_all"]);
                transaction_all.push(await element["transaction_all"]);
              });

              await income_all.forEach(async (element) => {
                totalincome_all += parseInt(element);
              });

              await transaction_all.forEach(async (element) => {
                totaltransaction_all += parseInt(element);
              });

              resolve({
                totalincome_all: totalincome_all,
                totaltransaction_all: totaltransaction_all,
              });

              income_all = [];
              transaction_all = [];
              totalincome_all = 0.0;
              totaltransaction_all = 0.0;
            } else {
              resolve({
                totalincome_all: 0,
                totaltransaction_all: 0,
              });
            }
          } else {
            resolve({
              totalincome_all: 0,
              totaltransaction_all: 0,
            });
          }
        },
        (error) => {
          reject(false);
        }
      );
    });
  }

  async TotalPerDay(user_id, form, to) {
    return new Promise((resolve, reject) => {
      this.db
        .getServiceJson({
          type: "perdayreport",
          user_id: user_id,
          form: form,
          to: to,
        })
        .then(
          async (data: any) => {
            if (data != null) {
              if (data.status == 200) {
                let income = [];
                let transaction = [];
                let totalincome = 0.0;
                let totaltransaction = 0.0;

                await data.result.forEach(async (element) => {
                  income.push(await element["income_money"]);
                  transaction.push(await element["transaction"]);
                });

                await income.forEach(async (element) => {
                  totalincome += parseInt(element);
                });

                await transaction.forEach(async (element) => {
                  totaltransaction += parseInt(element);
                });

                resolve({
                  totalincome: totalincome,
                  totaltransaction: totaltransaction,
                });

                income = [];
                transaction = [];
                totalincome = 0.0;
                totaltransaction = 0.0;
              } else {
                resolve({
                  totalincome: 0,
                  totaltransaction: 0,
                });
              }
            } else {
              resolve({
                totalincome: 0,
                totaltransaction: 0,
              });
            }
          },
          (error) => {
            reject(false);
          }
        );
    });
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: "bottom",
    });
    await toast.present();
  }
}



