import { Component } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AlertController, NavController, NavParams, ToastController } from 'ionic-angular';
import { ServiceProvider } from '../../providers/service/service';
import * as moment from 'moment';

/**
 * Generated class for the AdddevicePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-adddevice',
  templateUrl: 'adddevice.html',
})
export class AdddevicePage {

  public uid: any;
  public momentjs: any = moment;
  public date: any;
  public time: any;

  public user_id: any;
  public deviceForm: FormGroup;
  public deviceError: string;

  constructor(
    public navParams: NavParams,
    public navCtrl: NavController,
    public db: ServiceProvider,
    public formBuilder: FormBuilder,
    public toastCtrl: ToastController,
    public alertCtrl: AlertController
  ) {

    this.uid = localStorage.getItem('uidwallet');
    this.user_id = this.navParams.get('user_id')
    
    this.date = this.momentjs().format('DD/MM/YYYY');
    this.time = this.momentjs().format('HH:mm:ss');

    this.deviceForm = formBuilder.group({
      macaddress: ['', Validators.compose([Validators.required])],
      boardname: ['', Validators.compose([Validators.required])],
      createdate: [this.momentjs().format('YYYY-MM-DD'), Validators.compose([Validators.required])]
    });
  }


  async ionViewDidLoad() {
    await console.log('ionViewDidLoad AdddevicePage');
  }

  async Device() {

    const confirm = await this.alertCtrl.create({
      title: '<img src="../../assets/background/logo.png" />',
      message: 'คุณต้องการเพิ่มอุปกรณ์ใช่หรือไม่?',
      cssClass: 'alertCustomCssdetail',
      buttons: [
        {
          text: 'OK',
          handler: () => {

            let data = this.deviceForm.value;
            if (!data.macaddress || !data.boardname) {
              return;
            }

            let credentials = {
              type: "adddevice",
              user_id: this.user_id,
              macaddress: data.macaddress.toUpperCase(),
              boardname: data.boardname,
              status: 0,
              amounts: 0,
              date: this.date,
              datestamp: this.date + " " + this.time,
              createdate: this.date,
            };

            // console.log(credentials);

            this.db.getServiceJson(credentials).then(async (data: any) => {
              // console.log(data);
              if (data != null) {
                if (data.status == 200) {
                  if (data.result) {
                    this.notifications('เพิ่มอุปกรณ์สำเร็จ')
                    this.navCtrl.pop();
                  }
                } else {
                  this.notifications("macaddress ถูกใช้ไปแล้ว");
                  // this.deviceError = "macaddress ถูกใช้ไปแล้ว";
                }
              }
            });
          }
        },
        {
          text: 'CANCEL',
          handler: async () => {

          }
        }
      ]
    });
    await confirm.present();
  }

  async notifications(text) {
    const toast = await this.toastCtrl.create({
      message: text,
      duration: 1500,
      position: 'bottom'
    });
    await toast.present();
  }

}



